.library-box {
  display: flex;
  margin: 0 auto;
}
.library-btn {
  width: 148px;
  height: 42px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: #fff;
  border: 1px solid #fff;
  background: transparent;
  cursor: pointer;
  border-radius: 5px;
  text-transform: uppercase;

  line-height: 1.33;
  &:not(:last-child) {
    margin-right: 16px;
  }
}
.accent-btn {
  background: #ff6b01;
  border: none;
}
